import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { SichuanComponent } from './components/sichuan/sichuan.component';
import { ShanxiComponent } from './components/shanxi/shanxi.component';
import { MainComponent } from './components/main/main.component';
import { ProductComponent } from './components/product/product.component';
import { ZhejiangComponent } from './components/zhejiang/zhejiang.component';

import { ChongqingComponent } from './components/chongqing/chongqing.component';
import { AhoutoursComponent } from './components/ahoutours/ahoutours.component';
const routes: Routes = [
  {path: '', component: MainComponent,
   children: [
    {path: '', component: ProductComponent},
    {path: 'sichuan', component: SichuanComponent},
    {path: 'shanxi', component: ShanxiComponent},
    {path: 'zhejiang', component: ZhejiangComponent},
    {path: 'chongqing', component: ChongqingComponent},
    {path: 'aboutours', component: AhoutoursComponent},
  ]
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
